







import { defineComponent } from '@vue/composition-api'

export default defineComponent({
  // name: 'PageName'
  components: {},
  computed: {
    series() {
      const array = new Array(4)
      for (let i = 0; i < array.length; i++) {
        array[i] = 0
      }
      this.$store.getters['api/gData']('wind_direction_average').forEach((elem: any) => {
        if (elem.y > 0) {
          array[elem.y - 1]++
        }
      })

      return array
    }
  }
})
