



























import { defineComponent } from '@vue/composition-api';

let mode: any = '';
export default defineComponent({
  data() {
    return {
      wifiOptions: [],
      data: {
        hostname: '',
        mode,
        ssid: '',
        password: '',
        ipMode: '',
        ipAddress: '',
        options: {
          ipOptions: [
            { value: 'auto', label: 'Automtico' },
            { value: 'manual', label: 'Manual' }],
          modeOptions: [
            { label: 'Punto de acceso', value: 'ap' },
            { label: 'Estacin', value: 'sta' }
          ]
        }
      },
      isMounted: false
    };
  },
  methods: {
    onSubmit() {
      let form = {
        ssid: this.data.ssid,
        password: this.data.password,
        mode: this.data.mode.value == 'ap',
        dhcp: (<any>this.data.ipMode).value === 'auto',
        ip: this.data.ipAddress,
        gateway: this.data.ipAddress
      };

      this.$axios('net', {
        method: 'POST',
        data: form
      }).catch(e => {
        console.log(e.message);
        this.$q.notify({
            message: e.message,
            color: 'negative'
          }
        );
      });
    },
    filterFn(val: any, update: any) {
      if (this.wifiOptions.length > 0) {
        update();
        return;
      }

      update(() => {
        this.getNetworks().catch(e => {
          console.log(e.message);
        });
      });
    },
    async getNetworks() {
      let response: any = await this.$axios.get('/wifis').catch(e => {
        console.log(e.message);
        return;
      });
      this.wifiOptions = response.data;
      console.log(this.wifiOptions);
    }
  },
  computed: {},
  created(): void {
    this.getNetworks().catch(e => {
      console.log(e.message);
    });
  }
});
