





























import { defineComponent } from '@vue/composition-api'
import FreeMemory from 'components/FreeMemory.vue'
import { Directory, Filesystem } from '@capacitor/filesystem'

export default defineComponent({
  // name: 'PageName'
  components: {
    FreeMemory
  },
  data() {
    return {
      used: 0,
      total: 0
    }
  },
  mounted(): void {
    this.$axios.get('/storage').then(resp => {
      console.log(resp.data)
      this.used = resp.data.used
      this.total = resp.data.total
    }).catch(e => {
      console.log(e.message)
    })
  },
  computed: {
    freeMemory() {
      let result: number
      result = this.used / this.total * 100
      if (isNaN(result)) {
        result = 0
      }
      return result
    },
    formatted(): string {
      return this.freeMemory.toFixed(0)
    }
  },
  methods: {
    freeMemoryFormat(sizeInBytes: number) {
      let i = -1
      let byteUnits = [' kB', ' MB', ' GB', 'TB', ' PB', 'EB']

      do {
        sizeInBytes = sizeInBytes / 1024
        i++
      } while (sizeInBytes > 1024)

      return Math.max(sizeInBytes, 0.1).toFixed(1) + byteUnits[i]
    },
    formatSDCard() {
      this.$q.dialog({
        title: 'Confirmar',
        message: 'Esta accin borrar todos los datos almacenados en la SD. Esta accin no es reversible',
        cancel: true
      }).onOk(() => {
        this.$axios.get('/format').then(resp => {
          console.log(resp.data)
          this.$q.notify({
            message: 'Borrando tarjeta sd',
            color: 'positive'
          })
        }).catch(e => {
          this.$q.notify({
            message: 'error al borrar tarjeta sd',
            color: 'negative'
          })
          console.log(e.message)
        })
      })
    },
    requestDownload() {
      Filesystem.writeFile({
        data: 'me cago en todos tus muertos',
        path: `lecturas_${new Date().toISOString()}.txt`,
        directory: Directory.Cache
      }).then(result => {
        console.log(result)
        this.$q.notify({
          message: 'lol',
          color: 'positive',
          position: 'top'
        })
      }).catch(error => {
        console.log(error)
        this.$q.notify({
          message: `${error} stuff`,
          color: 'negative',
          position: 'top'
        })
      })
      this.$axios.get('/logs-global').then(async (response) => {
        // const url = window.URL.createObjectURL(new Blob(([response.data])))
        // const link = document.createElement('a')
        // link.href = url
        // link.setAttribute('download', `lecturas_${new Date().toISOString()}.txt`)
        // document.body.appendChild(link)
        // link.click()

        await Filesystem.writeFile({
          data: response.data,
          path: `lecturas_${new Date().toISOString()}.txt`,
          directory: Directory.Documents
        })
      }).catch((error) => console.log(error))
    }
  }
})
