


















import { defineComponent, ref } from '@vue/composition-api'

const frequencies = [
  {
    label: '15m', value: 15
  }, {
    label: '30m', value: 30
  }, {
    label: '1h', value: 60
  }, {
    label: '2h', value: 60 * 2
  }, {
    label: '5h', value: 60 * 5
  }, {
    label: '10h', value: 60 * 10
  }, {
    label: '1d', value: 60 * 24
  }, {
    label: '1w', value: 60 * 24 * 7
  }
]

export default defineComponent({
  data: function() {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const vm: any = this
    return {
      ip: '',
      buttons: [
        { label: 'establecer conexin', onClick: vm.testConn },
        { label: 'Sincronizar Fecha y Hora', onClick: vm.timeSync }
        // { label: 'resetear configuracin', onClick: vm.resetConfiguration },
        // { label: 'Modo Ahorro', onClick: vm.activateSavingMode }
      ]
    }
  },
  mounted(): void {
    this.ip = this.getUri()
  },
  methods: {
    updateUri(value: string) {
      this.ip = value
      this.$store.commit('api/changeMasterUri', value)
      this.$store.dispatch('api/changeMaster').catch(e => {
        console.log(e.message)
      })
    },
    getUri() {
      return this.$store.state.api.masterUri
    },
    testConn() {
      this.$axios.get(`http://${this.$store.state.api.masterUri}/`).then(resp => {
        console.log(resp.status)
        this.updateUri(this.$store.state.api.masterUri)
        this.$q.notify({
          message: 'Conexin establecida',
          color: 'positive',
          timeout: 1000,
          position: 'top'
        })
      }).catch(e => {
        console.log(e.message)
        this.$q.notify({
          message: e.message,
          color: 'negative',
          timeout: 1000,
          position: 'top'
        })
      })
    },
    timeSync() {
      this.$store.dispatch('api/syncTime').catch(e => {
        console.log(e.message)
      })
    },
    resetConfiguration() {
      this.$q.dialog({
        title: 'Confirmar',
        message: 'Esta accin restablecer los valores por defecto del dispositivo. Est accin no es reversible',
        cancel: true
      }).onOk(() => {
        this.$axios.get('/reset').then(resp => {
          console.log(resp.data)
          this.$q.notify({
            message: 'valores de fbrica restablecidos',
            color: 'positive'
          })
        }).catch(e => {
          this.$q.notify({
            message: 'error al reestablecer valores de fbrica',
            color: 'negative'
          })
          console.log(e.message)
        })
      })
    },
    activateSavingMode() {
      this.$q.dialog({
        title: 'Confirmar',
        message: 'Activar modo ahorro?',
        cancel: true,
        persistent: true
      }).onOk(() => {
        this.$axios.get('/battery/save').then(() => {
          this.$q.notify({
            message: 'Modo ahorro activado con exito',
            color: 'positive',
            position: 'top'
          })
        }).catch(() => {
          this.$q.notify({
            message: 'No es posible activar el modo ahorro',
            color: 'negative',
            position: 'top'
          })
        })
      }).onCancel(() => {
        console.log('Cancelled')
      })
    },
    async saveFreqs() {
      console.log(this.selectedFreq.value)
      const response = await this.$axios.post('/frequency', { frequency: this.selectedFreq.value }).catch(() => {
        this.$q.notify({
          message: 'Error al cambiar la frecuencia',
          color: 'negative',
          position: 'top'
        })
      })
      if (response) {
        this.$q.notify({
          message: 'Frecuencia cambiada con exito',
          color: 'positive',
          position: 'top'
        })
      }
    }
  },
  computed: {
    masterUri: {
      get() {
        return this.ip
      },
      set(value: string) {
        this.ip = value
        this.$store.commit('api/changeMasterUri', value)
      }
    }
  },
  setup() {
    return {
      frequencyOptions: frequencies,
      selectedFreq: ref(frequencies[0])
    }
  }
})
